<template>
    <div class="p-formgroup-inline">
        <div class="p-field-checkbox">
            <label>Total Eggs</label>
            <p-input-number v-model="form.total" showButtons />
        </div>
        <div class="p-field-checkbox">
            <label>M&#8545;</label>
            <p-input-number v-model="form.m2" showButtons />
        </div>
        <div class="p-field-checkbox">
            <label>M&#8544;</label>
            <p-input-number v-model="form.m1" showButtons />
        </div>
        <div class="p-field-checkbox">
            <label>GV</label>
            <p-input-number v-model="form.gv" showButtons />
        </div>
        <div class="p-field-checkbox">
            <label>Deg.</label>
            <p-input-number v-model="form.atr" showButtons />
        </div>
        <div class="p-field-checkbox">
            <label>Egg Cleaning by</label>
            <p-dropdown
                v-model="form.eggCleaningBy"
                :options="embryologistOptions"
                optionLabel="name"
                optionValue="id"
            />
        </div>
        <div class="p-field-checkbox">
            <label>Egg Retrival by</label>

            <p-multi-select
                v-model="form.eggRetrivalBy"
                :options="embryologistOptions"
                optionLabel="name"
                optionValue="id"
                style="width: 250px"
            />

            <!-- <p-dropdown
                v-model="form.eggRetrivalBy"
                :options="embryologistOptions"
                optionLabel="name"
                optionValue="id"
            /> -->
        </div>
    </div>
</template>

<script>
import { computed, inject, onMounted, onUnmounted, reactive } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const store = useStore();
        // const form = reactive({
        //     total: null,
        //     m2: false,
        //     m1: false,
        //     gv: false,
        //     atr: false,
        //     eggCleaningBy: null,
        //     eggRetrivalBy: null
        // });

        const form = inject("aspirationForm");

        const fillForm = (data) => {
            form.total = data.total;
            form.m2 = data.m2;
            form.m1 = data.m1;
            form.gv = data.gv;
            form.atr = data.atr;
            form.eggCleaningBy = data.eggCleaningBy;

            if (data.eggRetrivalBy === null || data.eggRetrivalBy.trim() === "") {
                form.eggRetrivalBy = [];
            } else {
                form.eggRetrivalBy = data.eggRetrivalBy.trim().split("|");
            }
            form.comment = data.comment;
            form.expComment = data.expComment;
        };

        const embryologistOptions = computed(
            () => store.state.doctor.embryologists
        );

        const toast = useToast();
        const save = () => {

            let sEggRetrivalBy = null;
            if (form.eggRetrivalBy.constructor === Array) {
                if(form.eggRetrivalBy.length > 0) {
                    sEggRetrivalBy = form.eggRetrivalBy.join("|");
                } else {
                    sEggRetrivalBy = null;
                }
            } else {
                sEggRetrivalBy = form.eggRetrivalBy;
            }

            store.dispatch("fertilization/saveFertilizaionFollAspiration", {
                ...form,
                eggRetrivalBy: sEggRetrivalBy,
                cycleId: store.state.artCycle.selectedCycle.cycleId,
                patientId: store.state.artCycle.patient.id
            }).then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Saved successfully",
                    life: 3000
                });
            })
            .catch(() => {})
            .finally(() => {});
        };

        onMounted(() => {
            store
                .dispatch(
                    "fertilization/getFertilization",
                    store.state.artCycle.selectedCycle.cycleId
                )
                .then((data) => {
                    fillForm(data);
                });

            emitter.on("cycle-monitoring:aspiration-form-save", save);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring:aspiration-form-save", save);
        });

        return {
            form,
            embryologistOptions
        };
    }
};
</script>

<style scoped>
.p-inputnumber,
.p-dropdown {
    height: 24px;
}
.p-inputnumber :deep() .p-inputtext {
    width: 50px;
}
</style>
